import React, {Component} from 'react';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';

import logo from '../../Images/logo.png';

import { HeaderBox, HeaderText, NavigationContainer, HumburgerIcon, ActiveLink } from './styles';

import Sidebar from './sidebar';
// import Submenu from './submenu';

class Header extends Component {
    constructor(props){
        super(props);
        this.state = { showMenu: false, showSubMenu: false };
    }

    toggleMenu = ()  => {
        this.setState(prevState => ({showMenu: !prevState.showMenu}));
    };

    toggleSubMenu =() => {
        this.setState(prevState => ({showSubMenu: !prevState.showSubMenu}))
    };

    render() {
        const { history } = this.props;

        return(
            <HeaderBox>
                <picture style={{ display: 'flex'}} onClick={() => history.push('/')}>
                    <img src={logo} alt="Soultrix Infotech" />&nbsp;
                    <HeaderText>
                        &nbsp;<strong>Soultrix Infotech</strong>
                    </HeaderText>
                </picture>
                <div>
                    <NavigationContainer
                        onClick={() => history.push('/contact')}>
                        {(history.location.pathname === '/contact') ? <ActiveLink>CONTACT</ActiveLink> : 'CONTACT'}
                    </NavigationContainer>
                    <NavigationContainer
                        onClick={() => history.push('/about')}>
                        {(history.location.pathname === '/about') ? <ActiveLink>ABOUT</ActiveLink> : 'ABOUT'}
                    </NavigationContainer>
                    <NavigationContainer
                        onClick={() => history.push('/why-us')}>
                        {(history.location.pathname === '/why-us') ? <ActiveLink>WHY US</ActiveLink> : 'WHY US'}
                    </NavigationContainer>
                    <NavigationContainer
                        onClick={() => history.push('/services')}>
                        {(history.location.pathname === '/services') || (history.location.pathname.includes('services')) ? <ActiveLink>SERVICES</ActiveLink> : 'SERVICES'}
                    </NavigationContainer>
                    <NavigationContainer
                        onClick={() => history.push('/')}>
                        {(history.location.pathname === '/') ? <ActiveLink>HOME</ActiveLink> : 'HOME'}
                    </NavigationContainer>
                </div>
                <div>
                    <HumburgerIcon onClick={this.toggleMenu}>
                        <i className="fa fa-bars" aria-hidden="true"></i>
                    </HumburgerIcon>
                </div>
                {
                    this.state.showMenu ? <Sidebar history={history} toggleMenu={this.toggleMenu}/> : null
                }
                {/*{*/}
                    {/*this.state.showSubMenu ? <Submenu history={history}/> : null*/}
                {/*}*/}
            </HeaderBox>
        );
    }
}

Header.propTypes = {
    history: PropTypes.object.isRequired,
};

export default withRouter(Header);
