import React, {Component } from 'react';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';

import { SideBarSection, Menu} from "./styles";

class Sidebar extends Component {
    constructor(props){
        super(props);
        this.state = { showSidebar: true };
    }

    toggleSideBar = ()  => {
        const { toggleMenu } = this.props;
        this.setState({showSidebar: false});
        toggleMenu();
    };

    render() {
        const { history } = this.props;
        const { showSidebar } = this.state;

        return (
            <SideBarSection>
                {  showSidebar ?
                    <Menu active={1} onClick={() => {history.push('/'); this.toggleSideBar()}}>Home</Menu> : <Menu active={0}/>
                }
                { showSidebar ?
                    <Menu active={1} onClick={() => {history.push('/services'); this.toggleSideBar()}}>Services</Menu> : <Menu active={0}/>
                }
                { showSidebar ?
                    <Menu active={1} onClick={() => {history.push('/why-us'); this.toggleSideBar()}}>Why Us</Menu> : <Menu active={0}/>
                }
                { showSidebar ?
                    <Menu active={1} onClick={() => {history.push('/about'); this.toggleSideBar()}}>About</Menu> : <Menu active={0}/>
                }
                { showSidebar ?
                    <Menu active={1} onClick={() => {history.push('/contact'); this.toggleSideBar()}}>Contact</Menu> : <Menu active={0}/>
                }
            </SideBarSection>
        );
    }
}

Sidebar.propTypes = {
    history: PropTypes.object.isRequired,
    toggleMenu: PropTypes.func,
};

export default withRouter(Sidebar);